.hero {
	padding: 8rem 0 8rem;
	padding-left: 17.5rem;
	background-image: linear-gradient(
		135deg,
		rgba($color-primary-light, 0.8),
		rgba($color-secondary-light, 0.5)
	);
	&__content {
		max-height: 60rem;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	&__text-box {
		padding-top: 8rem;
		width: 100%;
	}
}
